.ldBar {
  position: relative;
}
.ldBar.label-center > .ldBar-label {
  position: relative;
  font-size: 100px;
  font-weight: 100;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-shadow: 0 0 3px #fff;
}
.ldBar-label:after {
  font-size: 40px;
  font-weight: 100;
  content: '%';
  display: inline;
  top: -45px;
  position: relative;
}
.ldBar.no-percent .ldBar-label:after {
  content: '';
}

.ldBar path.mainline {
  stroke-width: 1.5;
  stroke: #09f;
}
.ldBar path.baseline {
  stroke-width: 8;
  stroke: #f1f2f3;
  stroke-linecap: round;
  filter: url(#custom-shadow);
}
